import React from 'react';
import {
  Route, Redirect, Switch,
} from 'react-router';
import PrivateRoute from 'components/privateRoute';
import Box from 'primitive-components/box';

import Login, { PATH as loginPath } from 'scenes/login';
import Corporate, { PATH as corporatePath } from 'scenes/corporate';
import User, { PATH as userPath } from 'scenes/user';

const App = () => (
  <Box height="100vh" width="100vw">
    <Switch>
      <Route path={loginPath} component={Login} />
      <PrivateRoute path={corporatePath} component={Corporate} />
      <PrivateRoute path={userPath} component={User} />
      <Route path="*" render={() => <Redirect to={loginPath} />} />
    </Switch>
  </Box>
);

export default App;
