import React from 'react';
import PropTypes from 'prop-types';
import { useIntl } from 'react-intl';
import styled, { useTheme } from 'styled-components';
import {
  typography, color, display, layout,
} from 'styled-system';
import { ToShortDate } from 'helpers/date';
import { rgba } from 'polished';
import Button from 'components/button';
import Box from 'primitive-components/box';

import BillsActions from './BillsActions';

const Container = styled(Box)`
  width: 100%;
  height: inherit;
  max-height: inherit;
  overflow-x: auto;
`;

const Table = styled('table')`
  border-collapse: collapse;
  width: 100%;
  ${typography}
`;

const THead = styled('thead')`
  color:${({ theme }) => theme.colors.tableTextColor || '#000000'};
`;

const Th = styled('th')`
  padding: 0.5rem 0.7rem 1rem;
  text-align:left;
`;

const Td = styled('td')`
  width:1px;
  white-space:nowrap;
  padding: 12px;
  text-align:left;
  ${color}
`;

const Tr = styled('tr')`
  &:hover {
    background-color: ${({ theme }) => rgba(theme.colors.tableHead, 0.05) || '#FFFFFF'};
  }
  white-space: nowrap;
  ${color}
  ${display}
  ${layout}
`;

const ButtonError = () => <Button p="3.5px 7px" rounded fontSize="xs" design="error">Con error</Button>;

const BillsTable = ({ bills }) => {
  const theme = useTheme();
  const { formatMessage: t } = useIntl();

  const trBackgroundColor = rgba(theme.colors.tableHead, 0.05) || '#FFFFFF';

  return (
    <Container>
      <Table fontSize="sm">
        <THead>
          <Tr bg={trBackgroundColor}>
            <Th>{t({ id: 'actions' })}</Th>
            <Th>{t({ id: 'serie' })}</Th>
            <Th>{t({ id: 'folio' })}</Th>
            <Th>{t({ id: 'date' })}</Th>
            <Th>{t({ id: 'issuing_rfc' })}</Th>
            <Th>{t({ id: 'receiver_rfc' })}</Th>
            <Th>{t({ id: 'state' })}</Th>
            <Th>{t({ id: 'total' })}</Th>
            <Th>{t({ id: 'uuid_fiscal_folio' })}</Th>
          </Tr>
        </THead>
        <tbody>
          {bills && bills.map((bill) => {
            const { estado } = bill;
            const lastState = estado[estado.length - 1].estado;

            return (
              <Tr key={bill.uuid}>
                <Td><BillsActions bill={bill} /></Td>
                <Td color={theme.colors.tableTextColor || '#000000'}>{bill.serie}</Td>
                <Td>{bill.folio}</Td>
                <Td>{ToShortDate(bill.date, 'string')}</Td>
                <Td>{bill.rfcIssuer}</Td>
                <Td>{bill.rfcReceiver}</Td>
                <Td>{lastState ? 'Recibido' : <ButtonError />}</Td>
                <Td>{`$${bill.total === '0' ? '0.00' : bill.total}`}</Td>
                <Td>{bill.uuid}</Td>
              </Tr>
            );
          })}
        </tbody>
      </Table>
    </Container>
  );
};

BillsTable.propTypes = {
  bills: PropTypes.arrayOf(PropTypes.shape({
    serie: PropTypes.string,
    folio: PropTypes.string,
    date: PropTypes.string,
    addressPdf: PropTypes.string,
    addressXml: PropTypes.string,
    rfcIssuer: PropTypes.string,
    rfcReceiver: PropTypes.string,
    total: PropTypes.number,
    uuid: PropTypes.string,
  })),
};

export default BillsTable;
