import React from 'react';
import PropTypes from 'prop-types';

import PrimitiveButton from 'primitive-components/button';
import styled, { css } from 'styled-components';
import {
  flexbox,
  background,
  border,
  color,
  layout,
  position,
  shadow,
  space,
  typography,
  compose,
} from 'styled-system';

const common = css`
  transition: all 0.5s ease 0s;
  padding: 8px 15px;
  ${compose(
    flexbox,
    background,
    border,
    color,
    layout,
    position,
    shadow,
    space,
    typography,
  )}
`;

const Shadow = styled(PrimitiveButton)`
  box-shadow: ${({ theme }) => theme.shadows.xl};
  ${common};
  &:hover {
    box-shadow: ${({ theme }) => theme.shadows.xxl};
  }
`;

const Error = styled(PrimitiveButton)`
  background-color: ${({ theme }) => theme.colors.errorButtonBackground};
  color: ${({ theme }) => theme.colors.errorButtonText};
  ${common};
  &:hover {
    background-color: ${({ theme }) => theme.colors.errorButtonHover};
  }
`;

const Success = styled(PrimitiveButton)`
  background-color: ${({ theme }) => theme.colors.successButtonBackground};
  color: ${({ theme }) => theme.colors.successButtonText};
  ${common};
  &:hover {
    background-color: ${({ theme }) => theme.colors.successButtonHover};
  }
`;

const Primary = styled(PrimitiveButton)`
  background-color:${({ theme }) => theme.colors.primaryButtonBackground};
  color:${({ theme }) => theme.colors.primaryButtonText};
  ${common};
  &:hover{
    background-color:${({ theme }) => theme.colors.primaryButtonHover};
  }
`;

const Info = styled(PrimitiveButton)`
  background-color:${({ theme }) => theme.colors.infoButtonBackground};
  color:${({ theme }) => theme.colors.infoButtonText};
  ${common};
  &:hover{
    background-color:${({ theme }) => theme.colors.infoButtonHover};
  }
`;

const components = {
  shadow: Shadow,
  error: Error,
  success: Success,
  primary: Primary,
  info: Info,
};

const Button = ({ design = 'shadow', rounded, ...remainderProps }) => {
  const Component = components[design];
  const borderRadius = rounded ? '60px' : '0px';
  return <Component fontWeight="sem" borderRadius={borderRadius} {...remainderProps} />;
};

Button.propTypes = {
  design: PropTypes.oneOf(['shadow', 'error', 'success', 'primary', 'info']),
  rounded: PropTypes.bool,
};

export default Button;
