import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Checkbox from 'primitive-components/checkbox';
import Input from 'components/input';
import styled from 'styled-components';
import Popover from '@material-ui/core/Popover';

const Label = styled(Text)`
  margin-bottom: 7px;
  white-space: nowrap;
`;

const InputContainer = styled(Box)`
    background-color: ${({ theme }) => theme.colors.dropdownSelectBackground};
    border-radius: 5rem;
    padding: 10px 20px;
    cursor: pointer;
`;

const InputText = styled(Text)`
  color: ${({ theme }) => theme.colors.dropdownSelectText};
  white-space: nowrap;
`;

const ItemContainer = styled(Box)`
  margin: 0.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
`;

const Scrollable = styled(Box)`
  overflow-y: auto;
  max-height: 12rem;
`;

const defaultSelectedItems = [];

const DropDownSelect = ({
  label, enableSearch, items, onSelect, selected, ...remainderProps
}) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedItems, setSelectedItems] = useState(defaultSelectedItems);
  const [searchText, setSearchText] = useState('');
  const hasItems = items?.length > 0;

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleSelect = (select) => {
    const exist = selectedItems.includes(select);
    const updatedSelectedItems = exist ? selectedItems.filter((item) => item !== select) : [...selectedItems, select];
    setSelectedItems(updatedSelectedItems);

    onSelect?.(updatedSelectedItems);
  };

  const getInputText = () => {
    const { length } = selectedItems;
    if (length === 0) return 'Nothing selected';
    return length === 1 ? selectedItems[0] : `${length} items selected`;
  };

  useEffect(() => {
    if (anchorEl) setSearchText('');
  }, [anchorEl]);

  useEffect(() => {
    setSelectedItems(defaultSelectedItems);
  }, [items]);

  useEffect(() => {
    setSelectedItems(selected || defaultSelectedItems);
  }, [selected]);

  return (
    <Box {...remainderProps}>
      <Label fontSize="sm">{label}</Label>
      <InputContainer centeredContent onClick={handleOpen}>
        <InputText fontSize="xs" fontWeight="bld">{getInputText()}</InputText>
      </InputContainer>

      {hasItems && (
        <Popover
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'center',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'center',
          }}
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <Scrollable>
            {enableSearch && <Input value={searchText} onChange={({ target: { value } }) => setSearchText(value)} />}
            {items.map((item, index) => {
              if (!item.startsWith(searchText)) return null;
              return (
                <ItemContainer key={index} onClick={() => handleSelect(item)}>
                  <Text fontSize="sm">{item}</Text>
                  <Checkbox ml="auto" backgroundColor="transparent" checked={selectedItems.includes(item)} />
                </ItemContainer>
              );
            })}
          </Scrollable>
        </Popover>
      )}
    </Box>
  );
};

DropDownSelect.propTypes = {
  items: PropTypes.arrayOf(PropTypes.string),
  onSelect: PropTypes.func,
  selected: PropTypes.arrayOf(PropTypes.string),
  label: PropTypes.string,
  enableSearch: PropTypes.bool,
};

export default DropDownSelect;
