import { makerApi } from 'api';
import createSagaMiddleware from 'redux-saga';
import sagas from './sagas';

let tasks = [];

export const sagaMiddlewareAPI = createSagaMiddleware();

export default (authData) => {
  const { signInUserSession: { idToken: { jwtToken } } } = authData;

  if (tasks.length) {
    tasks.forEach((task) => task?.cancel?.());
  }
  const sagaServices = { api: makerApi({ jwtToken }) };
  tasks = sagas.map((saga) => sagaMiddlewareAPI.run(saga, sagaServices));
};
