import { NAME } from './constants';

export const SEARCH_CFDI = `${NAME}.SEARCH_CFDI`;
export const SearchCFDI = (data) => ({
  type: SEARCH_CFDI,
  payload: data,
});

export const SEARCH_CFDI_BUSY = `${NAME}.SEARCH_CFDI_BUSY`;
export const SearchCFDIBusy = () => ({
  type: SEARCH_CFDI_BUSY,
  payload: null,
});

export const SEARCH_CFDI_FAILED = `${NAME}.SEARCH_CFDI_FAILED`;
export const SearchCFDIFailed = () => ({
  type: SEARCH_CFDI_FAILED,
  payload: null,
});

export const SEARCH_CFDI_SUCCESS = `${NAME}.SEARCH_CFDI_SUCCESS`;
export const SearchCFDISuccess = (data) => ({
  type: SEARCH_CFDI_SUCCESS,
  payload: data,
});

export const FETCH_RFCS_SUCCESS = `${NAME}.FETCH_RFCS_SUCCESS`;
export const SearchRFCsSuccess = (data) => ({
  type: FETCH_RFCS_SUCCESS,
  payload: data,
});
