import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import DownIcon from 'primitive-components/icons/Down';
import UpIcon from 'primitive-components/icons/Up';
import Button from 'components/button';
import { Accordion, AccordionDetails, AccordionSummary } from '@material-ui/core';

import DateRange from './DateRange';
import DropDownSelect from './DropDownSelect';

const IconWrapper = ({ icon: Icon }) => <Icon size="15px" />;
IconWrapper.propTypes = { icon: PropTypes.func };

const SummaryContainer = styled(Box)`
  display: flex;
  height: fit-content;
  align-items: center;
`;

const DetailsContainer = styled(Box)`
  padding: 1.25rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
`;

const StyledDropDownSelect = styled(DropDownSelect)`
  margin-bottom: 2rem;
  padding: 0px 15px;
`;

const BillsFilter = ({
  onSubmit,
  rfcs,
  filters,
  ...remainderProps
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [currentFilters, setCurrentFilters] = useState({});

  const handleChangeFilter = (type, select) => setCurrentFilters({ ...currentFilters, [type]: select });
  const handleChangeDateRange = ({ endDate, startDate }) => setCurrentFilters({ ...currentFilters, dateIn: startDate, dateEnd: endDate });

  const handleSubmitFilter = () => onSubmit?.(currentFilters);

  useEffect(() => {
    setCurrentFilters(filters);
  }, [filters]);

  return (
    <Box {...remainderProps}>
      <Accordion>
        <AccordionSummary
          aria-controls="bills-filters-header"
          id="bills-filters"
          onClick={() => setIsOpen(!isOpen)}
        >
          <SummaryContainer>
            <IconWrapper icon={isOpen ? UpIcon : DownIcon} />
            <Text ml="1rem" fontSize="sm">Filtrar búsqueda</Text>
          </SummaryContainer>
        </AccordionSummary>
        <AccordionDetails>
          <DetailsContainer>
            {rfcs?.length && (
              <StyledDropDownSelect
                label="RFC Receptor"
                enableSearch
                items={rfcs}
                // selected={rfcReceiver}
                // onSelect={(select) => handleChangeFilter('rfcReceiver', select)}
                selected={currentFilters?.rfcIssuer} // TODO check type rfc to request 
                onSelect={(select) => handleChangeFilter('rfcIssuer', select)} // TODO check type rfc to request 
              />
            )}
            <StyledDropDownSelect
              label="Estado"
              onSelect={(() => {})}
            />
            <StyledDropDownSelect
              label="Serie"
              enableSearch
              onSelect={(() => {})}
            />
            <StyledDropDownSelect
              label="Número de Folio"
              enableSearch
              onSelect={(() => {})}
            />
            <StyledDropDownSelect
              label="UUID Folio Fiscal"
              enableSearch
              onSelect={(() => {})}
            />
            <DateRange onChange={handleChangeDateRange} mb="32px" />
            <Button
              design="info"
              p="10px 20px"
              mb="10px"
              height="fit-content"
              onClick={handleSubmitFilter}
            >
              Buscar
            </Button>
          </DetailsContainer>
        </AccordionDetails>
      </Accordion>
    </Box>
  );
};

BillsFilter.propTypes = {
  onSubmit: PropTypes.func,
  filters: PropTypes.shape({
    rfcIssuer: PropTypes.arrayOf(PropTypes.string),
    rfcReceiver: PropTypes.arrayOf(PropTypes.string),
  }),
  rfcs: PropTypes.arrayOf(PropTypes.string),
};

export default BillsFilter;
