import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useIntl } from 'react-intl';
import { actions as cfdiActions, selectors as cfdiSelectors } from 'store/cfdi';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import styled from 'styled-components';
import BillsFilter from 'components/bills-filter';

const {
  // getAllBills,
  getRFCs,
} = cfdiSelectors;

const HomeContainer = styled(Box)`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;
const Title = styled(Text)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 15px 30px 15px;
`;

const Home = () => {
  const { formatMessage: t } = useIntl();
  const dispatch = useDispatch();
  const rfcs = useSelector(getRFCs);
  // const bills = useSelector(getAllBills);

  const searchCFDI = () => dispatch(cfdiActions.SearchCFDI(rfcs));

  useEffect(() => {
    searchCFDI();
  }, []);

  return (
    <HomeContainer>
      <Title fontSize="lg" fontWeight="bld">{t({ id: 'reports' })}</Title>
      <BillsFilter />
    </HomeContainer>
  );
};

export default Home;
