import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import useSelectors from 'helpers/useSelectors';
import { parseSearch, updateQueriesToSearch } from 'helpers/router';
import { splitCommaSeparatedArray } from 'helpers/general';
import { push } from 'connected-react-router';
import { useIntl } from 'react-intl';
import { useLocation } from 'react-router';
import { actions as cfdiActions, selectors as cfdiSelectors } from 'store/cfdi';
import { selectors as configSelectors } from 'store/config';
import styled from 'styled-components';

import BillsTable from 'components/bills-table';
import BillsFilter from 'components/bills-filter';
import DataBox from 'components/data-box';
import Pagination from 'components/pagination';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Spinner from 'primitive-components/spinner';
import { PATH as billsPath } from 'scenes/user/components/bills';

const {
  getBills, getPagination, getRFCs, getIsBusy,
} = cfdiSelectors;

const HomeContainer = styled(Box)`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;
const Title = styled(Text)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 15px 30px 15px;
`;

const Home = () => {
  const { formatMessage: t } = useIntl();
  const dispatch = useDispatch();
  const location = useLocation();
  const { pathname, search } = location;
  const { page, rfcIssuer, rfcReceiver } = parseSearch(search);

  const [
    rfcs,
    bills,
    isBusy,
    pagination,
    CFDIPageSize,
  ] = useSelectors(
    getRFCs,
    getBills,
    getIsBusy,
    getPagination,
    configSelectors.getCFDIPageSize,
  );

  const { total } = pagination;
  const hasBills = bills?.length > 0;
  const hasPagination = !!(page && total);

  const searchCFDI = (searchT) => dispatch(cfdiActions.SearchCFDI(searchT));
  const handleGoToBills = () => dispatch(push(billsPath));

  const handlePushSearch = (searchT) => dispatch(push({ pathname, search: searchT }));

  const handleChangePage = (pageT) => {
    const updatedSearch = updateQueriesToSearch({ page: pageT }, search);
    handlePushSearch(updatedSearch);
  };

  useEffect(() => {
    if (!page) handleChangePage(1);
  }, [page]);

  const handleChangeFilters = (filters) => {
    const updatedSearch = updateQueriesToSearch({ ...filters, page: 1 }, search);
    handlePushSearch(updatedSearch);
  };

  useEffect(() => {
    searchCFDI(search);
  }, [page, rfcIssuer, rfcReceiver]);

  const getFilters = () => ({
    rfcIssuer: splitCommaSeparatedArray(rfcIssuer),
    rfcReceiver: splitCommaSeparatedArray(rfcReceiver),
  });

  return (
    <HomeContainer>
      <Title fontSize="lg" fontWeight="bld">{t({ id: 'welcome_to_edx_inbox' })}</Title>
      <DataBox
        title={t({ id: 'bills' })}
        goToButton={{ label: t({ id: 'go_to_bills' }), onClick: handleGoToBills }}
        separator
      >
        <BillsFilter
          rfcs={rfcs}
          filters={getFilters}
          onSubmit={handleChangeFilters}
          mb="2rem"
        />

        <Box centeredContent height="15rem">
          {isBusy
            ? <Spinner />
            : (hasBills && <BillsTable bills={bills} />)}
        </Box>

        {hasPagination && (
          <Pagination
            onChange={handleChangePage}
            totalPages={Math.ceil(total / CFDIPageSize)}
            page={Number(page)}
            ml="auto"
            mt="1rem"
          />
        )}
      </DataBox>
    </HomeContainer>
  );
};

export default Home;
