import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import PDFIcon from 'media/pdf.png';
import XMLIcon from 'media/xml.png';
import ZIPIcon from 'media/zip.png';
import FileIcon from 'media/file.png';

import Box from 'primitive-components/box';

const StyledImg = styled('img')`
  width: 1.5rem;
  cursor: pointer;
`;

const ActionIcon = ({ href = '#', icon, alt }) => (
  <a href={href}>
    <StyledImg src={icon} alt={alt} />
  </a>
);

ActionIcon.propTypes = {
  icon: PropTypes.string,
  alt: PropTypes.string,
  href: PropTypes.string,
};

/// ////////////////////////////////////////////////////////////////
const BillsActions = ({ bill }) => {
  const { addressXml } = bill;
  const realXMLPath = addressXml.split(',')[0];

  return (
    <Box display="flex">
      <ActionIcon icon={FileIcon} alt="FileIcon" />
      <ActionIcon icon={PDFIcon} alt="PDFIcon" />
      <ActionIcon icon={XMLIcon} alt="XMLIcon" href={realXMLPath} />
      <ActionIcon icon={ZIPIcon} alt="ZIPIcon" />
    </Box>
  );
};

BillsActions.propTypes = {
  bill: PropTypes.shape({
    addressXml: PropTypes.string,
  }),
};

export default BillsActions;
