import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { addDays } from 'date-fns';
import { DateRange as DateRangeC } from 'react-date-range';
import Input from 'components/input';
import Popover from '@material-ui/core/Popover';

import Box from 'primitive-components/box';
import styled from 'styled-components';
import { ToShortDate } from 'helpers/date';

import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file

const DateRangeContainer = styled(Box)`
    border: solid 1px gray;
    border-radius: 4px;
    padding: 1rem;
    background-color: white;
`;

const RangeToSrt = ({ startDate, endDate }) => `${ToShortDate(startDate, 'date')} - ${ToShortDate(endDate, 'date')}`;

const DateRange = ({ value, onChange, ...remainigProps }) => {
  const [range, setRange] = useState(value);
  const [anchorEl, setAnchorEl] = useState(null);

  const handleOpen = (event) => setAnchorEl(event.currentTarget);
  const handleClose = () => setAnchorEl(null);

  const handleOnChange = ({ selection }) => {
    setRange(selection);

    const { endDate, startDate } = selection;
    onChange?.({
      endDate: endDate.toISOString(),
      startDate: startDate.toISOString(),
    });
  };

  useEffect(() => {
    setRange(value);
  }, [value]);

  return (
    <>
      <Input
        label="Date"
        value={RangeToSrt(range)}
        readOnly
        onClick={handleOpen}
        {...remainigProps}
      />
      <Popover
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <DateRangeContainer>
          <DateRangeC
            editableDateInputs
            onChange={handleOnChange}
            moveRangeOnFirstSelection={false}
            ranges={[range]}
          />
        </DateRangeContainer>
      </Popover>
    </>
  );
};

DateRange.defaultProps = {
  value: {
    startDate: addDays(new Date(), -30),
    endDate: new Date(),
    key: 'selection',
  },
};

DateRange.propTypes = {
  onChange: PropTypes.func,
  value: PropTypes.shape({
    startDate: PropTypes.instanceOf(Date),
    endDate: PropTypes.instanceOf(Date),
  }),
};

export default DateRange;
