import React from 'react';
import PropTypes from 'prop-types';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';

import { push, getLocation } from 'connected-react-router';
import { useDispatch, useSelector } from 'react-redux';
import { useIntl } from 'react-intl';

import styled, { useTheme } from 'styled-components';

const Container = styled(Box)`
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-top: 1rem;
`;

const Item = ({ item }) => {
  const {
    icon: Icon, id, route,
  } = item;
  const { formatMessage: t } = useIntl();
  const dispatch = useDispatch();
  const { pathname } = useSelector(getLocation);
  const isActive = pathname === route;

  const handleOnClick = () => dispatch(push(route));

  const theme = useTheme();
  const iconColor = theme.colors[`${isActive ? 'sideBarIconActive' : 'sideBarIconInactive'}`];

  return (
    <Container onClick={handleOnClick}>
      <Icon color={iconColor} size="1.2rem" />
      <Text fontSize="sm" ml="1rem" color={theme.colors.sideBarText}>{t({ id })}</Text>
    </Container>
  );
};

Item.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.string,
    route: PropTypes.string,
    icon: PropTypes.func,
    subs: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.string,
      route: PropTypes.string,
    })),
  }),
};

export default Item;
