import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled, { css, useTheme } from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import LeftIcon from 'primitive-components/icons/Left';
import RightIcon from 'primitive-components/icons/Right';
import _ from 'lodash';

const Container = styled(Box)`
    display: flex;
    width: fit-content;
    align-items: center;
    padding: 7px;
`;

const commonStyles = css`
    cursor: pointer;
    margin: 0px 13px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
`;

const Item = styled(Text)`${commonStyles}`;
const ArrowContainer = styled(Box)`
    display: flex;
    align-items: center;
    ${commonStyles}
 `;

const ArrowItem = ({ icon: Icon, color, onClick }) => <ArrowContainer onClick={onClick}><Icon size="13px" color={color} /></ArrowContainer>;
ArrowItem.propTypes = { icon: PropTypes.func, color: PropTypes.string, onClick: PropTypes.func };

const Pagination = ({
  maxPagesBySection, totalPages, page, onChange, ...remainderProps
}) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [currentSection, setCurrentSection] = useState(0);
  const theme = useTheme();

  const remPages = totalPages % maxPagesBySection;
  const pagesAmountLastSection = remPages === 0 ? maxPagesBySection : remPages;

  const pageToSection = (p) => {
    const ratio = p / maxPagesBySection;
    return Number.isInteger(ratio) ? ratio - 1 : Math.floor(ratio);
  }; // 0, 1, ...

  const handleUpdate = (p) => {
    setCurrentSection(pageToSection(p));
    setCurrentPage(p);
  };

  const handleChangeCurrentPage = (p) => {
    if (p < 1 || p > totalPages) return;
    handleUpdate(p);
    onChange?.(p);
  };

  const getPagesBySection = () => (currentSection === pageToSection(totalPages) ? pagesAmountLastSection : maxPagesBySection);

  const pagesBySection = getPagesBySection();
  const startRange = currentSection * maxPagesBySection + 1;
  const endRange = startRange + pagesBySection;
  const range = _.range(startRange, endRange);

  useEffect(() => {
    handleUpdate(page || 1);
  }, [page]);

  return (
    <Container {...remainderProps}>
      <ArrowItem
        icon={LeftIcon}
        onClick={() => handleChangeCurrentPage(currentPage - 1)}
        color={theme.colors.pagination}
      />
      {range.map((i) => {
        const isActive = currentPage === i;
        return (
          <Item
            key={i}
            onClick={() => handleChangeCurrentPage(i)}
            color={theme.colors[isActive ? 'paginationActive' : 'pagination']}
            fontWeight={isActive ? 'bld' : 'reg'}
            fontSize="sm"
          >
            {i}
          </Item>
        );
      })}
      <ArrowItem
        icon={RightIcon}
        onClick={() => handleChangeCurrentPage(currentPage + 1)}
        color={theme.colors.pagination}
      />
    </Container>
  );
};

Pagination.defaultProps = {
  maxPagesBySection: 3,
};

Pagination.propTypes = {
  maxPagesBySection: PropTypes.number,
  totalPages: PropTypes.number,
  page: PropTypes.number,
  onChange: PropTypes.func,
};

export default Pagination;
