import qs from 'query-string';

export const parseSearch = (searchStr, arrayFormat = 'none') => qs.parse(searchStr, { arrayFormat });
export const stringifySearch = (searchObj, arrayFormat = 'none') => qs.stringify(searchObj, { arrayFormat });

export const updateQueriesToSearch = (queries, search) => stringifySearch({ ...parseSearch(search), ...queries }, 'comma');

export const existQuery = (name, search) => !!parseSearch(search)?.[name];
