import {
  reducer as reducerRS,
  name as nameRS,
  defaultStatus as defaultStatusRS,
} from './status';
import * as actions from './actions';
import { defaultRFCs, defaultCFDIs } from './constants';

const defaultState = {
  rfcs: defaultRFCs,
  cfdis: defaultCFDIs,
  [nameRS]: defaultStatusRS,
};

const reducer = (stateP = defaultState, action = {}) => {
  const state = { ...stateP, [nameRS]: reducerRS(stateP[nameRS], action) };

  switch (action.type) {
    case actions.SEARCH_CFDI_SUCCESS:
      return {
        ...state,
        cfdis: action.payload,
      };
    case actions.FETCH_RFCS_SUCCESS:
      return {
        ...state,
        rfcs: action.payload,
      };
    default:
      return state;
  }
};

export default reducer;
