import {
  createStore, compose, applyMiddleware, combineReducers,
} from 'redux';
import createSagaMiddleware from 'redux-saga';
import { createHashHistory } from 'history';
import { connectRouter, routerMiddleware } from 'connected-react-router';
import { intlReducer, updateIntl } from 'react-intl-redux';
import { defaultTranslations } from 'helpers/intl';
import { makerAuthApi } from 'api';
import { sagaMiddlewareAPI } from './makerApi';
import reducers from './reducers';
import { sagas as authSagas } from './auth';

const sagaMiddlewareAuthAPI = createSagaMiddleware();
const history = createHashHistory();

// Middleware
const routingMiddleware = routerMiddleware(history);
const middlewares = applyMiddleware(sagaMiddlewareAuthAPI, sagaMiddlewareAPI, routingMiddleware);

// Compose
const composeEnhancers = typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
  ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({})
  : compose;

// Enhancers
const enhancer = composeEnhancers(middlewares);
const enhancedReducers = combineReducers({
  ...reducers,
  router: connectRouter(history),
  intl: intlReducer,
});

// Store
const store = createStore(enhancedReducers, enhancer);

// Run sagas for authentication services
const sagaServices = { api: makerAuthApi() };
sagaMiddlewareAuthAPI.run(authSagas, sagaServices);

// Internationalization
store.dispatch(updateIntl(defaultTranslations));

// STARTING
store.dispatch({ type: 'STARTING' }); // Used to initialize any component

export default () => ({ store, history, defaultTranslations });
