import {
  takeEvery, call, put, select,
} from 'redux-saga/effects';
import { existQuery, updateQueriesToSearch } from 'helpers/router';
import _ from 'lodash';
import { selectors as configSelectors } from 'store/config';
import { fromServer } from './transmute';
import { getRFCs } from './selectors';
import * as actions from './actions';

function* checkDefaultSearch(search) {
  let updatedSearch = search;

  // pageSize
  const pageSize = yield select(configSelectors.getCFDIPageSize);
  updatedSearch = updateQueriesToSearch({ pageSize }, updatedSearch);

  // rfcIssuer
  if (!existQuery('rfcIssuer', updatedSearch)) {
    const rfcIssuer = yield select(getRFCs);
    updatedSearch = updateQueriesToSearch({ rfcIssuer }, updatedSearch);
  }

  return updatedSearch;
}

function* searchCFDI(api, { payload: search }) {
  try {
    const updatedSearch = yield call(checkDefaultSearch, search);

    yield put(actions.SearchCFDIBusy());
    const { data } = yield call(api.searchCFDI, updatedSearch);
    yield put(actions.SearchCFDISuccess(fromServer(data)));
  } catch (e) {
    console.error(e);
    yield put(actions.SearchCFDIFailed());
  }
}

export default function* rfcSaga({ api }) {
  yield takeEvery(actions.SEARCH_CFDI, searchCFDI, api);
}
