import { PATH as corporatePath } from 'scenes/corporate';
import { PATH as userPath } from 'scenes/user';
import { accessLevels } from './constants';

const redirects = {
  [accessLevels.CORPORATE]: corporatePath,
  [accessLevels.USER]: userPath,
};

export const accessLevelToRoute = (accessLevel) => redirects[accessLevel];
