import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Banner from 'components/banner';

const sideBarWidth = '15rem';
const bannerHeight = '65px';
const paddingContent = '1.5rem';

const BannerContainer = styled(Box)`
  position: fixed;
  top:0;
  left:0;
  right:0;
  height: ${() => bannerHeight};
`;
const SideBarContainer = styled(Box)`
  position: fixed;
  top: 0;
  left:0;
  bottom: 0;
  margin-top: ${() => bannerHeight};
  width: ${() => sideBarWidth};
`;

const ContentContainer = styled(Box)`
  margin-top: ${() => bannerHeight};
  padding: ${() => paddingContent};
  padding-left: ${() => `calc(${sideBarWidth} + ${paddingContent})`};
  background-color: rgb(242, 243, 248);
`;

const Layout = ({ sideBar: SideBar, content: Content }) => (
  <>
    <ContentContainer>
      <Content />
    </ContentContainer>
    <BannerContainer>
      <Banner />
    </BannerContainer>
    <SideBarContainer>
      <SideBar />
    </SideBarContainer>
  </>
);

Layout.propTypes = {
  sideBar: PropTypes.func,
  content: PropTypes.func,
};

export default Layout;
