import { createSelector } from 'reselect';
import _ from 'lodash';
import { NAME } from './constants';
import {
  getIsNotStarted,
  getIsBusy,
  getIsSucceeded,
  getIsFailed,
} from './status';

const getRFCs = (state) => state[NAME].rfcs || [];
const getCFDIs = (state) => state[NAME].cfdis || {};

const getBills = createSelector(
  getCFDIs,
  ({ bills }) => bills || [],
);

const getPagination = createSelector(
  getCFDIs,
  ({ pagination }) => pagination || {},
);
export {
  getRFCs,
  getBills,
  getPagination,
  getIsNotStarted,
  getIsBusy,
  getIsSucceeded,
  getIsFailed,
};
