import RequestStatus from 'helpers/requestStatus';

import * as actions from './actions';
import { NAME } from './constants';

const name = 'requestStatus';
const actionsRequestStatus = {
  NOT_STARTED: [
    actions.SEARCH_CFDI_SUCCESS, // TODO same to SUCCESS
  ],
  BUSY: [
    actions.SEARCH_CFDI_BUSY,
  ],
  SUCCEEDED: [
    // actions.SEARCH_BILLS_SUCCESS, // TODO same to NOT_STARTED
  ],
  FAILED: [
    actions.SEARCH_CFDI_FAILED,
  ],
};

const {
  reducer,
  defaultStatus,
  getIsNotStarted,
  getIsBusy,
  getIsSucceeded,
  getIsFailed,
} = RequestStatus({
  nestedPath: [NAME],
  storeName: name,
  actions: actionsRequestStatus,
});

export {
  reducer,
  name,
  defaultStatus,
  getIsNotStarted,
  getIsBusy,
  getIsSucceeded,
  getIsFailed,
};
