import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Box from 'primitive-components/box';
import Text from 'primitive-components/text';
import Separator from 'primitive-components/separator';
import Button from 'components/button';

const Container = styled(Box)`
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: white;
`;

const Header = styled(Box)`
  display: flex;
  align-items: center;
  padding: 1rem;
`;

const HeaderLabel = styled(Text)`
  color: ${({ theme }) => theme.colors.dataBoxText || '#000000'};
`;

const ContentContainer = styled(Box)`
  padding: 1rem;
  width: 100%;
`;

const DataBox = ({
  title, separator, goToButton, children,
}) => (
  <Container>
    <Header>
      <HeaderLabel>{title}</HeaderLabel>
      {!!goToButton && <Button ml="auto" rounded onClick={goToButton.onClick}>{goToButton.label}</Button>}
    </Header>
    {separator && <Separator />}
    <ContentContainer>
      {children}
    </ContentContainer>
  </Container>
);

DataBox.propTypes = {
  title: PropTypes.string,
  separator: PropTypes.bool,
  goToButton: PropTypes.shape({
    label: PropTypes.string,
    onClick: PropTypes.func,
  }),
  children: PropTypes.node.isRequired,
};

export default DataBox;
